/* eslint-disable camelcase */
/* eslint-disable camelcase */
<template>
  <div class="goods-container">
    <el-dialog
      :visible.sync="detailsVisible"
      width="393px"
      center
      :before-close="close"
      custom-class="dialog_style"
    >
      <el-carousel
        height="375px"
        :autoplay="false"
        trigger="click"
        @change="carouselChange"
        :indicator-position="indicatorPosition"
      >
        <el-carousel-item
          v-for="(item, index) in rowData.first_img"
          :key="index"
        >
          <video
            id="video"
            :src="item.url"
            height="375"
            width="375"
            preloader="auto"
            controls
            v-if="item.suffixVal == 1"
          >
            <p>你的浏览器不支持video标签</p>
          </video>
          <img :src="item.url" width="375px" height="375px" v-else>
        </el-carousel-item>
      </el-carousel>
      <div class="border_base_info">
        <div class="row_price">
          <!-- <span class="price05">返 {{rowData.coupons_price}}券</span> -->
          <span class="price01">￥</span><span class="price02">{{ rowData.market_price }}</span>
          <span class="price03">{{ rowData.integral_price }}积分</span>
          <span class="price04">{{ rowData.coupons_price }}券</span>
        </div>
        <div class="row_title">
          <span class="text">{{ rowData.goods_name }}</span>
          <!-- <span class="tip">新品上新</span> -->
        </div>
        <div class="row_num">
          <span>库存：{{ rowData.inventory }}</span>
          <span>已售：{{ rowData.sales }}</span>
        </div>
      </div>
      <div class="border_main">
        <div class="bm_title">
          产品介绍
        </div>
        <div>
          <div v-for="(item, index) in rowData.details" :key="index">
            <img :src="item">
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
    data() {
        return {
            // 是否显示幻灯片的 切换按钮 默认不显示
            indicatorPosition: "none",
        };
    },
    props: {
        detailsVisible: {
            type: Boolean,
            defautl: false,
        },
        rowData: {
            type: Object,
            default() {
                return {};
            },
        },
    },
    methods: {
        close() {
            // eslint-disable-next-line camelcase
            this.rowData.first_img = [];
            this.$emit("update:detailsVisible", false);
            if (document.getElementById("video")) {
                document.getElementById("video").pause();
            }
        },
        // 幻灯片切换时，进行相应判断
        carouselChange(index) {
            if (this.rowData.first_img[index].suffixVal === 1) {
                this.indicatorPosition = "none";
                document.getElementById("video").pause();
            } else {
                this.indicatorPosition = "";
            }
        },
    },
    components: {},
    watch: {
    // 监听父组件传递过来的值 然后进行相应判断 是否显示幻灯片按钮
        rowData(obj) {
            if (obj.first_img[0].suffixVal === 1) {
                this.indicatorPosition = "none";
            } else {
                this.indicatorPosition = "";
            }
        },
    },
};
</script>

<style lang="scss">
.dialog_style {
  display: flex;
  flex-direction: column;
  margin: 0 !important;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /*height:600px;*/
  max-height: calc(100% - 30px);
  max-width: calc(100% - 30px);
}
.dialog_style .el-dialog__header {
  position: absolute;
  right: 0;
  top: 0;
}
.dialog_style .el-dialog__body {
  flex: 1;
  overflow: auto;
}
.dialog_style .el-dialog__body {
  padding: 0;
}

.dialog_style {
  height: 700px;
  overflow: auto;
  background-color: #f6f6f6;
  img {
    vertical-align: middle;
    max-width: 100%;
    max-height: 100%;
  }

  .border_base_info {
    margin-bottom: 10px;
    padding: 10px;
    background: #ffffff;
    border-radius: 0px 0px 10px 10px;
  }
  .row_price {
    margin-bottom: 12px;
  }
  .row_price .price01 {
    color: #f95f2c;
    font-size: 16px;
    font-weight: bold;
    position: relative;
    top: 2px;
  }
  .row_price .price02 {
    color: #f95f2c;
    font-size: 22px;
    font-weight: bold;
    position: relative;
    top: 3px;
  }
  .row_price .price03 {
    margin-left: 5px;
    padding: 1px 5px;
    color: #f7961c;
    background: rgb(250, 236, 216);
    border-radius: 3px;
    font-size: 12px;
  }
  .row_price .price04 {
    margin-left: 5px;
    padding: 1px 5px;
    color: #f7961c;
    background: rgb(250, 236, 216);
    border-radius: 3px;
    font-size: 12px;
  }
  .row_price .price05 {
    float: right;
    margin-top: 9px;
    padding: 0 5px;
    color: #ffffff;
    background: #f95f2c;
    border: 1px solid #f95f2c;
    border-radius: 6px;
    font-size: 12px;
  }
  .row_title {
    margin-bottom: 10px;
  }
  .row_title .text {
    color: #222222;
    font-size: 16px;
    font-weight: bold;
    line-height: 20px;
  }
  .row_title .tip {
    margin-left: 5px;
    padding: 1px 3px;
    background: #f7961c;
    border-radius: 3px;
    color: #ffffff;
    font-size: 12px;
    line-height: 24px;
  }
  .row_num {
    font-size: 12px;
    color: #616262;
  }
  .row_num span {
    margin-right: 15px;
  }

  .border_main {
    margin-bottom: 20px;
    background-color: #fff;
    border-radius: 10px;
    overflow: hidden;
  }
  .border_main .bm_title {
    padding: 15px 0;
    color: #222222;
    font-size: 14px;
    text-align: center;
  }
}
</style>