<template>
  <div class="goods-container" v-if="goodsDetail.details">
    <el-dialog
      :visible.sync="dialogVisible"
      width="900px"
      center
      :before-close="close"
    >
      <el-form
        label-width="130px"
      >
        <el-form-item label="抢单商品分类">
          <el-select
            v-model="value"
            clearable
            placeholder="请选择"
          >
            <el-option
              v-for="(item,index) in qucklySort"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="商品名称">
          <div style="width:400px">
            <el-input v-model="goodsDetail.goods_name" />
          </div>
        </el-form-item>
        <el-form-item label="封面">
          <el-upload
            ref="faceImg"
            style="display:inline"
            action
            :http-request="UploadFaceImg"
            list-type="picture-card"
            multiple
          >
            <i
              slot="default"
              class="el-icon-plus"
            />
          </el-upload>
          <transition name="el-fade-in-linear">
            <span v-show="faceImgShow">
              <img
                style="height:150px;width:150px"
                :src="goodsDetail.cover_img"
              >
            </span>
          </transition>
        </el-form-item>
        <el-form-item label="上传视频/图片">
          <el-upload
            style="display:inline"
            action
            :http-request="Upload"
            list-type="picture-card"
            ref="uploadFirst"
          >
            <i
              slot="default"
              class="el-icon-plus"
            />
          </el-upload>
          <transition name="el-fade-in-linear">
            <span v-show="isFirstImgShow">
              <span>
                <img
                  v-for="(item,index) of goodsDetail.first_img.split(',')"
                  :key="index"
                  style="width:150px;height:150px"
                  :src="item"
                >
              </span>
            </span>
          </transition>
        </el-form-item>
        <el-form-item label="时间">
          <el-checkbox
            v-for="(item,index) of goodsDetail.period_time"
            :key="index"
            :label="item.time"
            v-model="item.checked"
            @change="selectTime"
          />
        </el-form-item>
        <el-form-item label="价格">
          <div style="width:300px">
            <el-input
              v-model="goodsDetail.original_price"
              placeholder="请输入价格"
              @input="changePrice"
            />
          </div>
        </el-form-item>
        <el-form-item label="折扣">
          <el-select
            v-model="sellValue"
            clearable
            placeholder="请选择"
            @change="onChange"
          >
            <el-option
              v-for="(item,index) in sellList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="折扣价">
          <div style="width:250px;display:flex">
            <el-input v-model="goodsDetail.market_price" /><span style="margin-left:10px">元</span>
          </div>
        </el-form-item>
        <el-form-item label="支付方式">
          <el-select v-model="currentPayWay">
            <el-option 
              v-for="(item,index) of payWay" 
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="规格">
          <div
            v-for="(item, index) in goodsDetail.specifications"
            :key="index"
            class="size"
          >
            <div class="title-container">
              <el-input style="width:250px" v-model="item.title" placeholder="请添加规格分类" />
              <span class="addRowItem" @click="addRowItem(index)">添加规格项</span>
              <span class="delete_btn" @click="deleteSizeItem(index)">
                删除
              </span>
            </div>
            <div class="innerInput" v-if="item.child.length > 0 ">
              <div v-for="(i, k) of item.child"
                   :key="k"
              >
                <el-input
                  style="width: 200px"
                  v-model="i.innerValue"
                  placeholder="请输入规格项,如颜色,尺寸"
                />
                <el-input
                  style="width: 200px"
                  v-model="i.cPrice"
                  placeholder="请输入价格"
                />
                <el-upload style="display:inline" :show-file-list="false" action="" :http-request="SizeItemUpload" :on-change="(file,fileList)=>{ hanldChange(file,fileList,index,k) }">
                  <el-button type="primary">
                    上传图片
                  </el-button>
                </el-upload>
              </div>
              
              <el-button
                icon="el-icon-delete"
                size="small"

                @click="deleteInnerItem(index)"
              />
              <el-button
                type="primary"
                icon="el-icon-plus"
                class="innerAdd"
                size="small"

                @click="addInnerItem(index)"
              />
            </div>
          </div>
          <el-button type="primary" @click="addSizeItem">
            添加规格
          </el-button>
        </el-form-item>
        <el-form-item label="库存">
          <div style="width:300px">
            <el-input v-model="goodsDetail.inventory" />
          </div>
        </el-form-item>
        <el-form-item label="排序">
          <div style="width:300px">
            <el-input v-model="goodsDetail.sort" />
          </div>
        </el-form-item>
        <el-form-item label="商品简介">
          <el-input
            type="textarea"
            :rows="5"
            v-model="goodsDetail.describe"
          />
        </el-form-item>
        <el-form-item label="商品详情">
          <el-upload
            style="display:inline"
            action
            :http-request="detailImgsUpload"
            list-type="picture-card"
            ref="uploadTwo"
          >
            <i
              slot="default"
              class="el-icon-plus"
            />
          </el-upload>
          <transition name="el-fade-in-linear">
            <span v-show="desImgShow">
              <img
                v-for="(item,index) of goodsDetail.details.split(',')"
                :key="index"
                style="height:150px;width:150px"
                :src="item"
              >
            </span>
          </transition>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="confirm"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { client, setImgName } from "@/config/oss";
export default {
    data() {
        return {
            value:"",
            sellValue:"",
            // 时间节点
            times:[],
            
            selectdTime:[],
            // 抢单商品分类
            qucklySort:[],
            // 折扣分类
            sellList:[],
            FirstImgShow:[],
            isFirstImgShow:true,
            faceImgShow:true,
            desImgs:[],
            desImgShow:true,
            coverImg:"",
            // 上传视频/图片
            sortImgUrl:"",
            // 规格列表项图片
            sizeItemImgUrl:"",
            // 详情图
            desImg:"",
            // 当前支付方式
            currentPayWay:"",
            // 支付方式
            payWay:[
                {
                    name:"现金支付",
                    id:11
                }
            ]
        };
    },
    created() {
        this.getTime();
        this.getGoodsSort();
        this.getSell();
    },
    props:{
        dialogVisible:{
            type:Boolean,
            defautl:false,
        },
        goodsDetail:{
            type:Object,
            default (){
                return {};
            }
        }
    },
    methods: {
        // 上传封面图
        async  UploadFaceImg (file){
            let url = await setImgName(client(),file,"faceImg");
            // eslint-disable-next-line camelcase
            this.coverImg = url;
            this.faceImgShow = false;

        },
        // 分类图片上传
        async Upload (file){
            let url =  await setImgName( client(),file,"sort");
            this.FirstImgShow.push(url);
            this.isFirstImgShow = false;
           

        },
        // 增加规格项列表数量
        addInnerItem(index){
            if( this.goodsDetail.specifications[index].child.length < 10){
                this.goodsDetail.specifications[index].child.push({
                    innerValue:"",
                    imageUrl:"",
                    cPrice:0
                });
            }
            

        },
        // 删除规格项列表数量
        deleteInnerItem (index){
            this.goodsDetail.specifications[index].child.pop();

        },
        addRowItem(index) {
            this.goodsDetail.specifications[index].child.push({
                innerValue: "",
                imageUrl:"",
                cPrice:0
            });
        },
        
        // 增加规格项
        addSizeItem (){
            if(!this.goodsDetail.specifications){
                this.goodsDetail.specifications = [];
            }
            this.goodsDetail.specifications.push(
                {
                    title:"",
                    child:[]
                }
            );
            console.log(this.goodsDetail.specifications);
        },
        // 删除规格项
        deleteSizeItem(index){
            this.goodsDetail.specifications.splice(index,1);
        },
        // 上传规格项目图
        async SizeItemUpload(file){
            let url = await setImgName(client(),file,"sizeImg");
            this.sizeItemImgUrl = url;

        },
        // 详情图片上传
        async detailImgsUpload (file){
            let url =  await setImgName( client(),file,"detailImg");
            this.desImgs.push( url);
            this.desImgShow = false;
           

        },
        // 获取上传商品的有效时间节点
        async getTime (){
            let res = await this.axios.get("/goods/config/per/list",{
                params:{
                    // 有效的时间节点
                    status:1
                }
            });
            this.times = res.data.list.map( item => {
                return {
                    time: item.start_time,
                    checked:false,
                    id:item.id,
                    status:item.status
                };
            });

        },
        // 获取抢单商品分类
        async getGoodsSort (){
            try {
                let res = await this.axios.get("/goods/config/nav/list",{
                    params:{
                        // 抢购
                        type:2,
                        pages: 1,
                        limit: 50,
                    }
                });
                this.qucklySort = res.data.list;

             
            } catch (error) {
                console.log(error);
            }

        },
        // 获取折扣
        async getSell (){
            try {
                let res = await this.axios.get("/goods/config/dis/list",{
                    params:{
                        status:1
                    }
                });
                this.sellList = res.data.list;
            } catch (error) {
                console.log(error);
            }
        },
        // 打完折扣之后的价格
        onChange  (value){
            
            let selectedSellItem = this.sellList.filter( item => item.id === value),
                price = (parseFloat(selectedSellItem[0].name) / 10) * Number(this.goodsDetail.original_price);
            // eslint-disable-next-line camelcase
            this.goodsDetail.market_price = price.toFixed(2);

        },
        // 打完折扣之后的价格
        changePrice (){
            let selectedSellItem = this.sellList.filter( item => item.id === this.sellValue || this.goodsDetail.discount_id),
                price = (parseFloat(selectedSellItem[0].name) / 10) * Number(this.goodsDetail.original_price);
            // eslint-disable-next-line camelcase
            this.goodsDetail.market_price = price.toFixed(2);
        },
        
        // 选择时间
        selectTime (){
            this.selectdTime = this.goodsDetail.period_time.map(item => {
                return {

                    id:item.id,
                    status: item.checked ? 1 : 0

                };
            });
        },
        checkoutInput (){
            // 是否选择抢单时间
            let hasTime =  this.selectdTime.filter( item => item.status === 1).length > 0;
            if(hasTime){
                return true;

            }
            this.$message.warning({
                message:"请选择抢单时间"
            });
            return false;

        },
        // 上传抢单商品
        async confirm (){
            this.selectTime();
            if(!this.checkoutInput()){
                return;
            }
            for(let i = 0;i < this.goodsDetail.specifications.length;i++){
                if(!this.goodsDetail.specifications[i].title){
                    this.$message.warning({
                        message:"请添加规格分类"
                    });
                    return;
                }
            }
            try {
                let res = await  this.axios.post("goods/update",{
                    "id":this.goodsDetail.id,
                    "pid":this.value || this.goodsDetail.pid ,
                    "lastPid": this.sellValue || this.goodsDetail.discount_id,
                    "cover_img": this.coverImg  ||  this.goodsDetail.cover_img,
                    "firstImg":this.FirstImgShow.join(",") || this.goodsDetail.first_img,
                    "specifications":this.goodsDetail.specifications || [],
                    "inventory":this.goodsDetail.inventory,
                    // 抢购
                    "type":1,
                    "discountId":this.sellValue || this.goodsDetail.discount_id ,
                    "oPrice":this.goodsDetail.original_price,
                    "mPrice":this.goodsDetail.market_price,
                    "describe": this.goodsDetail.describe,
                    "goodsName":this.goodsDetail.goods_name,
                    "sort":this.goodsDetail.sort,
                    "details": this.desImgs.join(",") || this.goodsDetail.details,
                    "periodTime":this.selectdTime,


                });
                this.close();
                this.$emit("getData");
                this.$message({
                    message:res.msg,
                    type:"success"
                });
            } catch (error) {
                console.log(error);
            }
         

        },
        close (){
            this.value = "";
            this.isFirstImgShow = true;
            this. desImgShow = true;
            this.faceImgShow = true;
            this.$emit("setDialogVisible");
            this.$refs.faceImg.clearFiles();
            this.$refs.uploadFirst.clearFiles();
            this.$refs.uploadTwo.clearFiles();
        }
    },
    components: {

    }
};
</script>

<style lang="scss" scoped>
.goods-container {
  .size {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    .title-container {
      display: flex;
      justify-content: space-between;
      width: 500px;
      .delete_btn {
        width: 100px;
        height: 35px;
        line-height: 35px;
        text-align: center;
        border: 1px solid rgba(0,0,0,0.2);
        border-radius: 5px;
        cursor: pointer;
      }
      .addRowItem {
        width: 130px;
        height: 37px;
        line-height: 37px;
        text-align: center;
        background:#409EFF ;
        border-radius: 5px;
        color: white;
        cursor: pointer;
      }
    }
    .innerInput {
      margin-top: 5px;
    }
    .innerAdd {
      margin-left: 10px;
    }
  }
  .price {
    margin-left: 30px;
  }
  .width {
    width: 300px;
  }
  .select {
    margin-left: 15px;
  }
  .input-container {
    height: 90px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
</style>
