<template>
  <div class="lookcontainer">
    <div class="header">
      <div
        v-for="(item, index) of goodsType"
        :key="index"
        :class="['commen',{'active':currentComponent === item.path}]"
        @click="currentComponent = item.path"
      >
        {{ item.title }}
      </div>
    </div>
    <div>
      <component :is="currentComponent" />
    </div>
  </div>
</template>

<script>
import First from "./qucklyGoodsFirst";
import Second from "./qucklyGoodsSecond";
export default {
    data() {
        return {
            goodsType: [{
              title:"一审",
              path:"First"
            },{
              title:"二审",
              path:"Second"
            }],
            currentComponent:"First",
        };
    },
    components: {
      Second,
      First
    },
};
</script>

<style lang="scss" scoped>
.lookcontainer {
  display: flex;
  flex-direction: column;
  .header {
    display: flex;
    justify-content: space-between;
    width: 320px;
    margin-bottom: 20px;
  }
  .commen {
    width: 130px;
    height: 37px;
    display: grid;
    place-items: center;
    border-radius: 6px;
    cursor: pointer;
  }
  .active {
    color: white;
    background: #f7961c;
  }
}
</style>
