<template>
  <div class="qucklyGoods">
    <Goods
      :dialog-visible="dialogVisible"
      @getData="getGoods"
      :goods-detail="goodsDetail"
      @setDialogVisible="setDialogVisible"
    />
    <Detail :details-visible.sync="detailsVisible" :row-data="rowData" />
    <header class="header">
      <div class="search-container">
        <el-date-picker
          type="date"
          v-model="startTime"
          placeholder="开始时间"
          :picker-options="pickerOptions"
        />
        <el-date-picker
          v-model="endTime"
          type="date"
          placeholder="结束时间"
          :picker-options="pickerOptions"
        />
        <div class="bus_name">
          <el-input placeholder="请输入商家名称" />
        </div>
        <div class="shop_name">
          <el-input placeholder="请输入商品名称" />
        </div>
        <el-select
          style="width: 100px"
          v-model="value"
          placeholder="商品状态"
          clearable
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
        <div class="search-btn" @click="serach">
          确定
        </div>
      </div>
      <div class="throughContainer">
        <div class="through" @click="pass(0)">
          批量驳回
        </div>
        <div class="through margin" @click="pass(1)">
          批量通过
        </div>
      </div>
    </header>
    <div class="table">
      <el-table
        :data="tableData.list"
        border
        style="width=100%"
        ref="table"
        :header-cell-style="{ 'text-align': 'center', color: 'black' }"
        :cell-style="{ 'text-align': 'center','padding':'5px 0' }"
        @selection-change="handleSelect"
      >
        <el-table-column type="selection" />
        <el-table-column prop="id" label="序号" />
        <el-table-column
          prop="goods_name"
          label="商品名称"
          :show-overflow-tooltip="true"
        />
        <el-table-column label="封面">
          <template slot-scope="scope">
            <img style="width: 40px; height: 40px" :src="scope.row.cover_img">
          </template>
        </el-table-column>
        <el-table-column prop="sort" label="排序" />

        <el-table-column label="库存" prop="inventory" />
        <el-table-column label="支付类型" prop="pay_type" />
        <el-table-column label="原价" prop="original_price" />
        <el-table-column label="市场价" prop="market_price" />
        <el-table-column label="积分价格" prop="is_integral" />
        <el-table-column label="优惠券价格" prop="coupons_price" />
        <el-table-column label="状态" prop="desStatus" />
        <el-table-column prop="created_at" label="创建时间" :show-overflow-tooltip="true" />
        <el-table-column prop="updated_at" label="更新时间" :show-overflow-tooltip="true" />
        <el-table-column width="300px">
          <template slot="header" :slot-scope="scope">
            审核
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              style="color: black"
              @click="modify(scope.row.id)"
            >
              编辑
            </el-button>
            <el-button
              type="primary"
              size="mini"
              @click="rowSee(scope.row)"
            >
              预览
            </el-button>
            <el-switch
              style="margin-left: 30px"
              v-model="scope.row.status"
              active-text="通过"
              @change="onChange(scope.row)"
              inactive-text="驳回"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="tableData.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click="next"
      />
    </div>
  </div>
</template>

<script>
import Goods from "./goods.vue";
import Detail from "@/components/details.vue";
export default {
    data() {
        return {
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() > Date.now();
                },
            },
            // 选中的商品状态
            options: [
                {
                    value: "选项1",
                    label: "黄金糕",
                },
                {
                    value: "选项2",
                    label: "双皮奶",
                },
                {
                    value: "选项3",
                    label: "蚵仔煎",
                },
                {
                    value: "选项4",
                    label: "龙须面",
                },
                {
                    value: "选项5",
                    label: "北京烤鸭",
                },
            ],
            value: "",
            // 开始时间
            startTime: "",
            // 结束时间
            endTime: "",
            scope: "scope",
            dialogVisible: false,
            rowData :{},
            detailsVisible: false,
            // 每页条目数
            limit: 11,
            // 获取哪一页数据
            pages: 1,

            tableData: [],
            // 商品详情
            goodsDetail: {},
            selectedIds: [],
            // 抢单有效时间
            times: [],
        };
    },
    created() {
        this.getGoods();
        this.getTime();
    },
    methods: {
    // 搜索
        serach() {
            console.log(111);
        },
        // 抢单商品列表
        async getGoods() {
            try {
                let res = await this.axios.get("/goods/list", {
                    params: {
                        limit: this.limit,
                        pages: this.pages,
                        //  抢单商品
                        type: 1,
                    },
                });
                this.tableData = Object.assign(res.data, {
                    list: res.data.list.map((item) => {
                        return Object.assign(item, {
                            status: item.audit === 1 ? true : false,
                            desStatus: item.audit === 1 ? "通过" : "审核中",
                        });
                    }),
                });
            } catch (error) {
                console.log(error);
            }
        },
        // 预览
        rowSee(row) {
            this.detailsVisible = true;
            this.rowData = JSON.parse(JSON.stringify(row));

            for (let i = 0; i < this.rowData.first_img.length; i++) {
                let url = this.rowData.first_img[i],
                    filename = url,
                    index = filename.lastIndexOf("."),
                    suffix = filename.substr(index + 1).toLowerCase(),
                    suffixVal = 0;
                if (
                    suffix === "mp4" ||
          suffix === "flv" ||
          suffix === "avi" ||
          suffix === "rm" ||
          suffix === "rmvb"
                ) {
                    suffixVal = 1;
                }
                this.rowData.first_img[i] = { url: url, suffixVal: suffixVal };
            }
        },
        // 批量操作
        handleSelect(value) {
            this.selectedIds = value.map((item) => item.id);
        },
        // 批量审核
        /**
     *  @param status
     *  1 通过
     *  0 驳回
     *
     */
        async pass(status) {
            if (this.selectedIds.length < 1) {
                return;
            }
            await this.axios.post("/goods/examine", {
                id: this.selectedIds,
                // 1 通过 0 驳回
                type: status,
            });
            this.$refs.table.clearSelection();
            this.getGoods();
        },
        // 修改
        async modify(id) {
            try {
                let res = await this.axios.get("/goods/details", {
                        params: {
                            id,
                        },
                    }),
                    _data = JSON.parse(res.data.list[0].period_time);
                this.times.forEach((item) => {
                    _data.forEach((k) => {
                        if (item.id === Number(k.id)) {
                            item.id = Number(k.id);
                            item.status = Number(k.status);
                            item.checked = Number(k.status) ? true : false;
                        }
                    });
                });
                this.goodsDetail = Object.assign(res.data.list[0], {
                    "period_time": this.times,
                });
                this.dialogVisible = true;
            } catch (error) {
                console.log(error);
            }
        },
        // 获取上传商品的有效时间节点
        async getTime() {
            let res = await this.axios.get("/goods/config/per/list", {
                params: {
                    // 有效的时间节点
                    status: 1,
                },
            });

            this.times = res.data.list.map((item) => {
                return {
                    time: item.start_time,
                    checked: false,
                    id: item.id,
                    status: item.status,
                };
            });
        },
        // 商品状态更改
        async onChange(item) {
            try {
                await this.axios.post("/goods/examine", {
                    id: [item.id],
                    //  audit 1 为商品审核通过 0 为驳回
                    type: item.audit === 0 ? 1 : 0,
                });
                this.getGoods();
            } catch (error) {
                console.log(error);
            }
        },
        // 上一页
        prev() {
            this.pages--;
            this.getGoods();
        },
        // 翻页
        changePage(page) {
            this.pages = page;
            this.getGoods();
        },
        // 下一页
        next() {
            this.pages++;
            this.getGoods();
        },
        setDialogVisible() {
            this.dialogVisible = false;
        },
    },
    components: {
        Goods,
        Detail
    },
};
</script>

<style lang="scss" scoped>
.qucklyGoods {
  .table {
    margin-top: 15px;
    min-height: 700px;
  }
  .header {
    display: flex;
    justify-content: space-between;
     @media screen and (max-width: 1600px) {
        flex-direction: column;
        height: 90px;
      }
    .search-container {
      display: flex;
      justify-content: space-between;
      width: 100%;
      max-width: 1300px;
     
      .search-btn {
        $height: 40px;
        width: 80px;
        height: $height;
        line-height: $height;
        text-align: center;
        color: white;
        cursor: pointer;
        background: #f7961c;
        border-radius: 6px;
      }
    }
    .throughContainer {
       width: 230px;
      display: flex;
      justify-content: space-between;
      @media screen and (max-width: 1600px) {
        align-self: flex-end;
      }
    }
    .through {
      $height: 35px;
      color: #f7961c;
      width: 100px;
      height: $height;
      display: inline-block;
      text-align: center;
      line-height: $height;
      border: 1px solid #f7961c;
      border-radius: 6px;
      cursor: pointer;
    }
    .margin {
      margin-left: 20px;
    }
  }
  .pagination {
    margin: 20px 0;
  }
}
</style>
